/**
 * @file services/page.tsx
 * @description 服务业务列表页面 - 显示所有可见的服务
 * @author Arran
 * @copyright 2025 Arran (SuMoChen)
 * @version 1.0.0
 * @created 2025-11-25
 */

'use client';

import { motion } from 'framer-motion';
import { Search, X, ShoppingBag, Tag as TagIcon } from 'lucide-react';
import { useEffect, useState } from 'react';
import { getServices, getServiceCategories, getSettings, getImageUrl, getPageText } from '@/lib/api';
import BlogNav from '@/components/BlogNav';
import Copyright from '@/components/Copyright';
import ServiceModal from '@/components/ServiceModal';
import Masonry from 'react-masonry-css';
import SEOMeta from '@/components/SEOMeta';
import PageBackground from '@/components/PageBackground';

interface Service {
  id: number;
  name: string;
  description: string;
  content: string;
  cover_image: string;
  price: string;
  category_id?: number;
  is_visible: boolean;
  is_recommended: boolean;
  sort_order: number;
  show_order_button: boolean;
  order_button_text?: string;
  order_button_url?: string;
  created_at: string;
  updated_at: string;
  Category?: {
    id: number;
    name: string;
    icon?: string;
  };
  specifications?: Array<{
    id: number;
    spec_name: string;
    spec_value: string;
    sort_order: number;
  }>;
}

interface ServiceCategory {
  id: number;
  name: string;
  description?: string;
  icon?: string;
  sort_order: number;
  is_visible: boolean;
}

export default function ServicesPage() {
  const [services, setServices] = useState<Service[]>([]);
  const [categories, setCategories] = useState<ServiceCategory[]>([]);
  const [selectedCategory, setSelectedCategory] = useState<number | null>(null);
  const [loading, setLoading] = useState(true);
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedService, setSelectedService] = useState<Service | null>(null);
  // 页面文本配置
  const [pageText, setPageText] = useState({
    title: '服务业务',
    description: '为您提供专业的服务解决方案',
    browserTitle: '服务业务',
    browserSubtitle: ''
  });
  const [pageTextLoaded, setPageTextLoaded] = useState(false);

  // 默认设置
  const defaultSettings = {
    themeType: 'default',
    footerCopyright: `© ${new Date().getFullYear()} 个人主页. All rights reserved.`,
    showTopNavbar: 'true',
    showWapSidebar: 'true',
    siteTitle: 'Xs-Blog'
  };
  const [settings, setSettings] = useState<any>(defaultSettings);

  useEffect(() => {
    fetchServices();
    fetchCategories();
    loadSettings();
  }, [selectedCategory]);

  // 设置页面标题（仅使用 browserTitle，不受网站标题、副标题影响）
  useEffect(() => {
    let title = pageText.browserTitle;

    if (selectedCategory && categories.length > 0) {
      const category = categories.find(cat => cat.id === selectedCategory);
      if (category) {
        title = `${category.name} - ${pageText.browserTitle}`;
      }
    }

    document.title = title;
  }, [selectedCategory, categories, pageText.browserTitle]);

  const loadSettings = async () => {
    try {
      // 并行加载设置和页面文本配置
      const [settingsResponse, pageTextResponse] = await Promise.all([
        getSettings(),
        getPageText('services')
      ]);

      if (settingsResponse.success && settingsResponse.data) {
        const settingsObj: any = {};
        settingsResponse.data.forEach((setting: any) => {
          settingsObj[setting.key] = setting.value;
        });
        setSettings({
          themeType: settingsObj.themeType || 'default',
          footerCopyright: settingsObj.footerCopyright || `© ${new Date().getFullYear()} 个人主页. All rights reserved.`,
          showTopNavbar: settingsObj.showTopNavbar || 'true',
          showWapSidebar: settingsObj.showWapSidebar || 'true',
          siteTitle: settingsObj.siteTitle || 'Xs-Blog'
        });
      }

      // 从新的 page_texts 表加载页面文本配置
      if (pageTextResponse.success && pageTextResponse.data) {
        setPageText({
          title: pageTextResponse.data.title || '服务业务',
          description: pageTextResponse.data.description || '为您提供专业的服务解决方案',
          browserTitle: pageTextResponse.data.browserTitle || '服务业务',
          browserSubtitle: pageTextResponse.data.browserSubtitle || ''
        });
        setPageTextLoaded(true);
      }
    } catch (error) {
      console.error('Failed to load settings:', error);
      setPageTextLoaded(true); // 即使失败也标记为已加载，使用默认值
    }
  };

  const fetchServices = async () => {
    try {
      setLoading(true);
      const params: any = {};
      if (selectedCategory) {
        params.category_id = selectedCategory;
      }

      const response = await getServices(params);
      const data = response.data;

      // API返回格式: { success: true, data: { services: [], pagination: {} } }
      if (data && data.services && Array.isArray(data.services)) {
        setServices(data.services);
      } else if (Array.isArray(data)) {
        // 兼容直接返回数组的情况
        setServices(data);
      } else {
        setServices([]);
      }
    } catch (error) {
      console.error('Failed to fetch services:', error);
      setServices([]);
    } finally {
      setLoading(false);
    }
  };

  const fetchCategories = async () => {
    try {
      const response = await getServiceCategories();
      const data = response.data;
      setCategories(Array.isArray(data) ? data : []);
    } catch (error) {
      console.error('Failed to fetch categories:', error);
      setCategories([]);
    }
  };

  const handleServiceClick = (service: Service) => {
    setSelectedService(service);
  };

  const handleCloseModal = () => {
    setSelectedService(null);
  };

  const handleSearch = (e: React.FormEvent) => {
    e.preventDefault();
  };

  const handleClearSearch = () => {
    setSearchQuery('');
  };

  // 基于搜索查询过滤服务
  const filteredServices = services.filter((service) => {
    if (!searchQuery.trim()) return true;

    const query = searchQuery.toLowerCase();
    const nameMatch = service.name?.toLowerCase().includes(query);
    const descMatch = service.description?.toLowerCase().includes(query);
    const priceMatch = service.price?.toLowerCase().includes(query);

    return nameMatch || descMatch || priceMatch;
  });

  const isBlogTheme = settings?.themeType === 'blog';
  const showTopNavbar = settings?.showTopNavbar !== 'false';
  const showWapSidebar = settings?.showWapSidebar !== 'false';

  // 显示搜索结果的提示
  const showSearchResults = searchQuery.trim() && filteredServices.length > 0;
  const showNoResults = searchQuery.trim() && filteredServices.length === 0;

  return (
    <main className="min-h-screen bg-bg-primary relative flex flex-col hide-scrollbar">
      <SEOMeta />
      {/* 背景图片 */}
      <PageBackground />
      {/* 顶部导航栏 - 此页面始终显示 */}
      <BlogNav showWapSidebar={showWapSidebar} />

      {/* 内容区域 */}
      <div className="relative z-0 flex-1 flex flex-col">
        {/* 给导航栏留出空间 */}
        <div className="h-16"></div>

        <div className="container mx-auto px-4 max-w-6xl py-8">
          {/* 页面标题 */}
          <div className="text-center mb-8">
            <h1 className="text-3xl font-bold text-text-primary mb-2">{pageText.title}</h1>
            <p className="text-text-secondary">{pageText.description}</p>
          </div>

          {/* 搜索框 */}
          <div className="mb-6">
            <form onSubmit={handleSearch}>
              <div className="relative max-w-2xl mx-auto">
                <Search className="absolute left-4 top-1/2 transform -translate-y-1/2 w-5 h-5 text-text-tertiary" />
                <input
                  type="text"
                  placeholder="搜索服务..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  className="w-full pl-12 pr-12 py-3 bg-bg-secondary border border-border-primary rounded-xl text-text-primary placeholder-text-tertiary focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent transition-all"
                  autoComplete="off"
                />
                {searchQuery && (
                  <button
                    type="button"
                    onClick={handleClearSearch}
                    className="absolute right-4 top-1/2 transform -translate-y-1/2 text-text-tertiary hover:text-text-primary transition-colors"
                    aria-label="清空搜索"
                  >
                    <X className="w-5 h-5" />
                  </button>
                )}
              </div>
            </form>
          </div>

          {/* 搜索结果显示 */}
          {showSearchResults && (
            <div className="mb-6 p-4 rounded-xl border max-w-2xl mx-auto" style={{ backgroundColor: 'var(--info-bg)', borderColor: 'var(--info-border)' }}>
              <div className="flex items-center justify-center gap-2">
                <span className="text-sm" style={{ color: 'var(--info-text)' }}>
                  搜索结果: 找到 {filteredServices.length} 个相关服务
                  <span className="font-medium ml-1">"{searchQuery}"</span>
                </span>
              </div>
            </div>
          )}

          {/* 无搜索结果提示 */}
          {showNoResults && (
            <div className="mb-6 p-4 bg-bg-tertiary rounded-xl border border-border-primary text-center max-w-2xl mx-auto">
              <span className="text-sm text-text-tertiary">
                没有找到包含 "{searchQuery}" 的服务
              </span>
            </div>
          )}

          {/* 分类筛选 */}
          {categories.length > 0 && (
            <div className="mb-8">
              <h3 className="text-sm font-medium text-text-secondary mb-3 flex items-center gap-2">
                <TagIcon className="w-4 h-4" />
                按分类筛选
              </h3>
              <div className="flex gap-2 flex-wrap">
                <button
                  onClick={() => setSelectedCategory(null)}
                  className={`px-4 py-2 rounded-lg text-sm font-medium transition-all ${
                    selectedCategory === null
                      ? 'bg-primary-500 text-white shadow-md'
                      : 'bg-bg-secondary text-text-secondary hover:bg-bg-tertiary border border-border-primary'
                  }`}
                >
                  全部
                </button>
                {categories.map((category) => (
                  <button
                    key={category.id}
                    onClick={() => setSelectedCategory(category.id)}
                    className={`px-4 py-2 rounded-lg text-sm font-medium transition-all ${
                      selectedCategory === category.id
                        ? 'bg-primary-500 text-white shadow-md'
                        : 'bg-bg-secondary text-text-secondary hover:bg-bg-tertiary border border-border-primary'
                    }`}
                  >
                    {category.icon && <span className="mr-1.5">{category.icon}</span>}
                    {category.name}
                  </button>
                ))}
              </div>
            </div>
          )}

          {/* 服务列表 */}
          {loading ? (
            <Masonry
              breakpointCols={{
                default: 6,
                1536: 6,
                1280: 5,
                1024: 4,
                768: 3,
                640: 2
              }}
              className="flex -ml-4 w-auto"
              columnClassName="pl-4 bg-clip-padding"
            >
              {[...Array(12)].map((_, index) => (
                <div key={index} className="bg-bg-secondary rounded-xl shadow-sm border border-border-primary overflow-hidden mb-4 animate-pulse">
                  <div className="aspect-square bg-bg-tertiary"></div>
                  <div className="p-3">
                    <div className="h-4 bg-bg-tertiary rounded w-3/4 mb-2"></div>
                    <div className="flex items-baseline justify-between">
                      <div className="h-5 bg-bg-tertiary rounded w-16"></div>
                      <div className="h-4 bg-bg-tertiary rounded w-12"></div>
                    </div>
                  </div>
                </div>
              ))}
            </Masonry>
          ) : filteredServices.length > 0 ? (
            <Masonry
              breakpointCols={{
                default: 6,
                1536: 6,
                1280: 5,
                1024: 4,
                768: 3,
                640: 2
              }}
              className="flex -ml-4 w-auto"
              columnClassName="pl-4 bg-clip-padding"
            >
              {filteredServices.map((service, index) => (
                <motion.div
                  key={service.id}
                  onClick={() => handleServiceClick(service)}
                  className="group bg-bg-secondary rounded-xl shadow-sm hover:shadow-xl hover:-translate-y-1 border border-border-primary transition-all duration-300 cursor-pointer overflow-hidden mb-4"
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ delay: index * 0.05 }}
                >
                  {/* 服务封面图 - 1:1 正方形 */}
                  <div className="relative aspect-square bg-bg-tertiary overflow-hidden">
                    {service.cover_image ? (
                      <img
                        src={getImageUrl(service.cover_image)}
                        alt={service.name}
                        className="w-full h-full object-cover group-hover:scale-110 transition-transform duration-300"
                        onError={(e) => {
                          const target = e.target as HTMLImageElement;
                          target.style.display = 'none';
                        }}
                      />
                    ) : (
                      <div className="w-full h-full flex items-center justify-center">
                        <ShoppingBag className="w-16 h-16 text-text-tertiary opacity-30" />
                      </div>
                    )}

                    {/* 推荐标签 */}
                    {service.is_recommended && (
                      <div className="absolute top-2 right-2 px-2 py-1 rounded text-xs font-semibold" style={{ background: 'linear-gradient(to right, var(--accent-gradient-from), var(--accent-gradient-to))', color: 'var(--accent-text)' }}>
                        推荐
                      </div>
                    )}
                  </div>

                  {/* 服务信息 */}
                  <div className="p-3">
                    <h3 className="text-base font-semibold text-text-primary group-hover:text-primary-500 transition-colors mb-2 line-clamp-1">
                      {service.name}
                    </h3>

                    {/* 价格 */}
                    <div className="flex items-baseline justify-between">
                      <span className="text-lg font-bold text-primary-600">
                        <span className="text-base font-normal mr-0.5">¥</span>{service.price}
                      </span>
                      {service.Category && (
                        <span className="text-xs text-text-tertiary px-2 py-0.5 bg-bg-tertiary rounded">
                          {service.Category.icon && <span className="mr-1">{service.Category.icon}</span>}
                          {service.Category.name}
                        </span>
                      )}
                    </div>
                  </div>
                </motion.div>
              ))}
            </Masonry>
          ) : (
            <div className="text-center py-12">
              <ShoppingBag className="w-16 h-16 text-text-tertiary opacity-30 mx-auto mb-4" />
              <p className="text-text-secondary">暂无服务</p>
            </div>
          )}
        </div>
      </div>

      {/* 页脚版权 */}
      <footer className="mt-auto border-t border-border-primary py-6 text-center text-sm text-text-tertiary">
        <p>{settings.footerCopyright}</p>
        <Copyright />
      </footer>

      {/* 服务详情弹窗 */}
      <ServiceModal
        service={selectedService}
        isOpen={!!selectedService}
        onClose={handleCloseModal}
      />
    </main>
  );
}
