'use client';

import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { X, ShoppingBag, ChevronRight, Sparkles } from 'lucide-react';
import ReactMarkdown from 'react-markdown';
import { getImageUrl } from '@/lib/api';

interface ServiceModalProps {
  service: {
    id: number;
    name: string;
    description: string;
    content: string;
    cover_image: string;
    price: string;
    category_id?: number;
    is_visible: boolean;
    is_recommended: boolean;
    sort_order: number;
    show_order_button: boolean;
    order_button_text?: string;
    order_button_url?: string;
    spec_title?: string;
    created_at: string;
    updated_at: string;
    Category?: {
      id: number;
      name: string;
      icon?: string;
    };
    specifications?: Array<{
      id: number;
      spec_name: string;
      spec_value: string;
      sort_order: number;
    }>;
  } | null;
  isOpen: boolean;
  onClose: () => void;
}

export default function ServiceModal({ service, isOpen, onClose }: ServiceModalProps) {
  const [imageError, setImageError] = useState(false);

  if (!service) return null;

  const showBottomOrderButton = service.show_order_button && service.order_button_url;

  return (
    <AnimatePresence>
      {isOpen && (
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          className="fixed inset-0 bg-black/70 backdrop-blur-md z-50 flex items-center justify-center p-3"
          onClick={onClose}
        >
          <motion.div
            initial={{ opacity: 0, scale: 0.9, y: 30 }}
            animate={{ opacity: 1, scale: 1, y: 0 }}
            exit={{ opacity: 0, scale: 0.9, y: 30 }}
            transition={{ type: 'spring', duration: 0.4, bounce: 0.2 }}
            className="bg-white dark:bg-gray-900 w-full max-w-[380px] rounded-2xl overflow-hidden shadow-2xl relative flex flex-col"
            style={{
              aspectRatio: '9/16',
              maxHeight: 'calc(100vh - 24px)'
            }}
            onClick={(e) => e.stopPropagation()}
          >
            {/* 关闭按钮 */}
            <button
              onClick={onClose}
              className="absolute top-3 right-3 z-30 w-8 h-8 flex items-center justify-center bg-black/30 hover:bg-black/50 backdrop-blur-sm rounded-full transition-all duration-200"
              aria-label="关闭"
            >
              <X className="w-4 h-4 text-white" />
            </button>

            {/* 可滚动内容区域 */}
            <div
              className={`flex-1 overflow-y-auto overscroll-contain hide-scrollbar ${showBottomOrderButton ? 'pb-20' : 'pb-4'}`}
            >

              {/* 封面图区域 - 1:1 */}
              <div className="relative w-full aspect-square bg-gray-100 dark:bg-gray-800">
                {service.cover_image && !imageError ? (
                  <img
                    src={getImageUrl(service.cover_image)}
                    alt={service.name}
                    className="w-full h-full object-cover"
                    onError={() => setImageError(true)}
                  />
                ) : (
                  <div className="w-full h-full flex items-center justify-center bg-gradient-to-br from-violet-500/20 to-fuchsia-500/20">
                    <ShoppingBag className="w-20 h-20 text-gray-300 dark:text-gray-600" />
                  </div>
                )}

                {/* 推荐标签 */}
                {service.is_recommended && (
                  <div className="absolute top-3 left-3 flex items-center gap-1 bg-gradient-to-r from-orange-500 to-rose-500 text-white px-2.5 py-1 rounded-full text-[11px] font-medium shadow-lg">
                    <Sparkles className="w-3 h-3" />
                    <span>精选推荐</span>
                  </div>
                )}

                {/* 分类标签 */}
                {service.Category && (
                  <div className="absolute top-3 right-12 flex items-center gap-1 bg-white/90 dark:bg-gray-800/90 backdrop-blur-sm px-2.5 py-1 rounded-full text-[11px] text-gray-600 dark:text-gray-300 shadow-sm">
                    {service.Category.icon && <span>{service.Category.icon}</span>}
                    <span>{service.Category.name}</span>
                  </div>
                )}
              </div>

              {/* 标题和价格区域 */}
              <div className="px-4 py-4">
                {/* 标题 */}
                <h2 className="text-lg font-bold text-gray-900 dark:text-white leading-snug mb-2">
                  {service.name}
                </h2>

                {/* 描述 */}
                {service.description && (
                  <p className="text-gray-500 dark:text-gray-400 text-sm leading-relaxed mb-3">
                    {service.description}
                  </p>
                )}

                {/* 价格 */}
                <div className="flex items-baseline gap-0.5">
                  <span className="text-rose-500 text-sm font-bold">¥</span>
                  <span className="text-rose-500 text-2xl font-bold tracking-tight">{service.price}</span>
                  {/* <span className="text-gray-400 text-xs ml-1">起</span> */}
                </div>
              </div>

              {/* 服务规格 */}
              {service.specifications && service.specifications.length > 0 && (
                <div className="px-4 mb-4">
                  <div className="flex items-center justify-between mb-3">
                    <h3 className="text-sm font-semibold text-gray-900 dark:text-white flex items-center gap-2">
                      <span className="w-1 h-4 bg-gradient-to-b from-violet-500 to-fuchsia-500 rounded-full"></span>
                      {service.spec_title || '服务规格'}
                    </h3>
                  </div>
                  <div className="space-y-2">
                    {service.specifications.sort((a, b) => a.sort_order - b.sort_order).map((spec) => (
                      <div
                        key={spec.id}
                        className="flex items-center justify-between py-2.5 px-3 bg-gray-50 dark:bg-gray-800/50 rounded-lg"
                      >
                        <span className="text-gray-500 dark:text-gray-400 text-sm">
                          {spec.spec_name}
                        </span>
                        <span className="text-gray-900 dark:text-white text-sm font-medium">
                          {spec.spec_value}
                        </span>
                      </div>
                    ))}
                  </div>
                </div>
              )}

              {/* 服务详情 */}
              {service.content && (
                <div className="px-4">
                  <div className="flex items-center justify-between mb-3">
                    <h3 className="text-sm font-semibold text-gray-900 dark:text-white flex items-center gap-2">
                      <span className="w-1 h-4 bg-gradient-to-b from-violet-500 to-fuchsia-500 rounded-full"></span>
                      服务详情
                    </h3>
                  </div>
                  <div className="prose prose-sm max-w-none text-gray-600 dark:text-gray-300 prose-headings:text-gray-900 dark:prose-headings:text-white prose-strong:text-gray-900 dark:prose-strong:text-white prose-a:text-violet-600 dark:prose-a:text-violet-400">
                    <ReactMarkdown
                      components={{
                        h1: ({ node, ...props }) => (
                          <h1 className="text-base font-bold mt-4 mb-2 text-gray-900 dark:text-white" {...props} />
                        ),
                        h2: ({ node, ...props }) => (
                          <h2 className="text-sm font-bold mt-3 mb-2 text-gray-900 dark:text-white" {...props} />
                        ),
                        h3: ({ node, ...props }) => (
                          <h3 className="text-sm font-semibold mt-3 mb-1.5 text-gray-800 dark:text-gray-100" {...props} />
                        ),
                        p: ({ node, ...props }) => (
                          <p className="mb-3 leading-relaxed text-sm text-gray-600 dark:text-gray-300" {...props} />
                        ),
                        ul: ({ node, ...props }) => (
                          <ul className="list-disc list-outside ml-4 mb-3 space-y-1.5 text-sm text-gray-600 dark:text-gray-300" {...props} />
                        ),
                        ol: ({ node, ...props }) => (
                          <ol className="list-decimal list-outside ml-4 mb-3 space-y-1.5 text-sm text-gray-600 dark:text-gray-300" {...props} />
                        ),
                        li: ({ node, ...props }) => (
                          <li className="text-sm leading-relaxed" {...props} />
                        ),
                        blockquote: ({ node, ...props }) => (
                          <blockquote
                            className="border-l-3 border-violet-500 pl-3 py-2 my-3 bg-violet-50 dark:bg-violet-900/20 rounded-r-lg text-sm italic"
                            {...props}
                          />
                        ),
                        code: ({ node, inline, ...props }: any) =>
                          inline ? (
                            <code
                              className="px-1.5 py-0.5 bg-gray-100 dark:bg-gray-800 rounded text-xs font-mono text-violet-600 dark:text-violet-400"
                              {...props}
                            />
                          ) : (
                            <code className="block p-3 bg-gray-100 dark:bg-gray-800 rounded-lg overflow-x-auto text-xs font-mono" {...props} />
                          ),
                        img: ({ node, ...props }) => (
                          <img
                            className="rounded-xl shadow-md my-3 max-w-full"
                            loading="lazy"
                            {...props}
                          />
                        ),
                        a: ({ node, ...props }) => (
                          <a
                            className="text-violet-600 dark:text-violet-400 hover:underline font-medium"
                            target="_blank"
                            rel="noopener noreferrer"
                            {...props}
                          />
                        ),
                      }}
                    >
                      {service.content}
                    </ReactMarkdown>
                  </div>
                </div>
              )}
            </div>

            {/* 底部固定下单按钮 */}
            {showBottomOrderButton && (
              <div className="absolute bottom-0 left-0 right-0 p-4 bg-gradient-to-t from-white via-white dark:from-gray-900 dark:via-gray-900 to-transparent pt-8">
                <a
                  href={service.order_button_url}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="flex items-center justify-center gap-2 w-full bg-gradient-to-r from-violet-600 to-fuchsia-600 hover:from-violet-500 hover:to-fuchsia-500 text-white py-3.5 rounded-xl font-semibold shadow-lg shadow-violet-500/30 hover:shadow-xl hover:shadow-violet-500/40 transform hover:scale-[1.02] active:scale-[0.98] transition-all duration-200"
                >
                  <span className="text-[15px]">{service.order_button_text || '立即下单'}</span>
                  <ChevronRight className="w-4 h-4" />
                </a>
              </div>
            )}
          </motion.div>
        </motion.div>
      )}
    </AnimatePresence>
  );
}
